import React, { useState, useEffect, useRef } from 'react';
import {
  Box,
  Paper,
  Typography,
  Stack,
  Alert,
  useTheme,
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  CircularProgress,
  LinearProgress
} from '@mui/material';
import { useMediaQuery } from '@mui/material';

import { alpha } from '@mui/material/styles';
import { Close } from '@mui/icons-material';
import { Trade, Calendar } from '../../types/dualWrite';
import { ScoreSettings, ScoreAnalysis } from '../../types/score';
import { DynamicRiskSettings } from '../../utils/dynamicRiskUtils';
import { scoreService } from '../../services/scoreService';

import { scrollbarStyles } from '../../styles/scrollbarStyles';
import ScoreCard from './ScoreCard';
import ScoreBreakdown from './ScoreBreakdown';
import ScoreHistory from './ScoreHistory';
import ScoreSettingsComponent from './ScoreSettings';
import TagPatternAnalysis from '../TagPatternAnalysis';
import RoundedTabs, { TabPanel } from '../common/RoundedTabs';
import { logger } from '../../utils/logger';
interface ScoreSectionProps {
  trades: Trade[];
  selectedDate: Date;
  calendarId: string;
  scoreSettings?: ScoreSettings;
  onUpdateCalendarProperty?: (calendarId: string, updateCallback: (calendar: Calendar) => Calendar) => Promise<Calendar | undefined>;
  // Dynamic risk settings
  accountBalance?: number;
  dynamicRiskSettings?: DynamicRiskSettings;
  allTags?: string[]; // Add allTags prop to receive calendar.tags
  timePeriod?: 'month' | 'year' | 'all'; // Parent chart time period to restrict available score periods
}



const ScoreSection: React.FC<ScoreSectionProps> = ({
  trades,
  selectedDate,
  calendarId,
  scoreSettings,
  onUpdateCalendarProperty,
  accountBalance,
  dynamicRiskSettings,
  allTags,
  timePeriod = 'all' // Default to 'all' if not provided
}) => {
  const theme = useTheme();
  const isXs = useMediaQuery(theme.breakpoints.down('sm'));
  const [activeTab, setActiveTab] = useState(0);
  const scorePeriod = 'monthly';
  const [historyPeriod, setHistoryPeriod] = useState<'daily' | 'weekly' | 'monthly' | 'yearly'>('monthly');
  const [settings, setSettings] = useState<ScoreSettings>(scoreSettings || scoreService.getSettings());
  const [selectedTags, setSelectedTags] = useState<string[]>(scoreSettings?.selectedTags || []);
  const [isSaving, setIsSaving] = useState(false);
  const [isCalculating, setIsCalculating] = useState(false);
  const [isLoadingHistory, setIsLoadingHistory] = useState(false);
  const [isLoadingMultiPeriod, setIsLoadingMultiPeriod] = useState(false);

  const [scoreAnalysis, setScoreAnalysis] = useState<ScoreAnalysis | null>(null);
  const [scoreHistory, setScoreHistory] = useState<any[]>([]);
  const [multiPeriodScores, setMultiPeriodScores] = useState<any>(null);

  // Cache for score history to avoid recalculating when switching tabs
  const scoreHistoryCache = useRef<Map<string, any[]>>(new Map());

  // State for breakdown modal
  const [breakdownModalOpen, setBreakdownModalOpen] = useState(false);
  const [selectedBreakdownData, setSelectedBreakdownData] = useState<{
    analysis: ScoreAnalysis;
    period: 'daily' | 'weekly' | 'monthly' | 'yearly';
  } | null>(null);

  // Initialize settings from calendar props when they change
  useEffect(() => {
    if (scoreSettings) {
      setSettings(scoreSettings);
      setSelectedTags(scoreSettings.selectedTags || []);
    }
  }, [scoreSettings]);

  // Update score service settings when local settings change
  useEffect(() => {
    scoreService.updateSettings(settings);
  }, [settings]);

  // Update score service settings when selected tags change
  useEffect(() => {
    const updatedSettings = { ...settings, selectedTags };
    scoreService.updateSettings(updatedSettings);
  }, [selectedTags, settings]);

  // Determine available score history periods based on parent chart time period
  const availablePeriods = React.useMemo((): ('daily' | 'weekly' | 'monthly' | 'yearly')[] => {
    switch (timePeriod) {
      case 'month':
        // For monthly view: only daily and weekly
        return ['daily', 'weekly'];
      case 'year':
        // For yearly view: daily, weekly, and monthly
        return ['daily', 'weekly', 'monthly'];
      case 'all':
      default:
        // For all time view: all periods available
        return ['daily', 'weekly', 'monthly', 'yearly'];
    }
  }, [timePeriod]);

  // Ensure current historyPeriod is valid when parent timePeriod changes
  useEffect(() => {
    if (!availablePeriods.includes(historyPeriod)) {
      // If current period is not available, switch to the most granular available period
      setHistoryPeriod(availablePeriods[availablePeriods.length - 1]);
    }
  }, [availablePeriods, historyPeriod]);

  // Update dynamic risk settings in score service
  useEffect(() => {
    if (accountBalance !== undefined) {
      scoreService.updateDynamicRiskSettings({
        account_balance: accountBalance,
        risk_per_trade: dynamicRiskSettings?.risk_per_trade,
        dynamic_risk_enabled: dynamicRiskSettings?.dynamic_risk_enabled,
        increased_risk_percentage: dynamicRiskSettings?.increased_risk_percentage,
        profit_threshold_percentage: dynamicRiskSettings?.profit_threshold_percentage
      });
    }
  }, [accountBalance, dynamicRiskSettings]);

  // Calculate current score analysis
  useEffect(() => {
    if (trades.length === 0) {
      setScoreAnalysis(null);
      return;
    }

    const calculateScoreAnalysis = async () => {
      setIsCalculating(true);
      try {
        // Ensure score service has the latest settings before calculation
        const updatedSettings = { ...settings, selectedTags };
        scoreService.updateSettings(updatedSettings);
        const analysis = await scoreService.calculateScore(trades, scorePeriod, selectedDate, updatedSettings);
        setScoreAnalysis(analysis);
      } catch (error) {
        logger.error('Error calculating score:', error);
        setScoreAnalysis(null);
      } finally {
        setIsCalculating(false);
      }
    };

    calculateScoreAnalysis();
  }, [trades, scorePeriod, selectedDate, selectedTags, settings]);

  // Calculate score history
  useEffect(() => {
    if (trades.length === 0) {
      setScoreHistory([]);
      setIsLoadingHistory(false);
      return;
    }

    const calculateScoreHistory = async () => {
      // Generate cache key based on calculation parameters
      const cacheKey = JSON.stringify({
        tradesCount: trades.length,
        historyPeriod,
        selectedTags: selectedTags.sort(),
        settingsKey: `${settings.thresholds.minTradesForScore}-${settings.weights.consistency}`,
        selectedDate: selectedDate.toISOString().split('T')[0],
        timePeriod
      });

      // Check cache first
      const cachedHistory = scoreHistoryCache.current.get(cacheKey);
      if (cachedHistory) {
        // Use cached data immediately without loading
        setScoreHistory(cachedHistory);
        setIsLoadingHistory(false);
        return;
      }

      setIsLoadingHistory(true);
      try {
        // Ensure score service has the latest settings before calculation
        const updatedSettings = { ...settings, selectedTags };
        scoreService.updateSettings(updatedSettings);

        // Determine number of periods based on parent time period and history period
        let periodsBack = 12;
        if (timePeriod === 'month') {
          // For monthly view, limit to current month
          if (historyPeriod === 'daily') {
            // Show days in the selected month (max ~31)
            periodsBack = 31;
          } else if (historyPeriod === 'weekly') {
            // Show weeks in the selected month (max 5)
            periodsBack = 5;
          } else if (historyPeriod === 'monthly') {
            // Show just the current month
            periodsBack = 1;
          }
        } else if (timePeriod === 'year') {
          // For yearly view, limit to current year
          if (historyPeriod === 'daily') {
            // Show days in the selected year (max ~365)
            periodsBack = 365;
          } else if (historyPeriod === 'weekly') {
            // Show weeks in the selected year (max 53)
            periodsBack = 53;
          } else if (historyPeriod === 'monthly') {
            // Show months in the selected year (12)
            periodsBack = 12;
          } else if (historyPeriod === 'yearly') {
            // Show just the current year
            periodsBack = 1;
          }
        } else {
          // For 'all' view, show last 12 periods
          periodsBack = 12;
        }

        let history = await scoreService.getScoreHistory(
          trades,
          historyPeriod,
          periodsBack,
          updatedSettings,
          selectedDate // Pass selected date as reference
        );

        // Filter history to only include periods within the parent time period bounds
        if (timePeriod === 'month') {
          // Only show scores from the selected month
          const monthStart = new Date(selectedDate.getFullYear(), selectedDate.getMonth(), 1);
          const monthEnd = new Date(selectedDate.getFullYear(), selectedDate.getMonth() + 1, 0);
          history = history.filter(entry => {
            const entryDate = new Date(entry.date);
            return entryDate >= monthStart && entryDate <= monthEnd;
          });
        } else if (timePeriod === 'year') {
          // Only show scores from the selected year
          const yearStart = new Date(selectedDate.getFullYear(), 0, 1);
          const yearEnd = new Date(selectedDate.getFullYear(), 11, 31);
          history = history.filter(entry => {
            const entryDate = new Date(entry.date);
            return entryDate >= yearStart && entryDate <= yearEnd;
          });
        }

        // Store in cache for future use
        scoreHistoryCache.current.set(cacheKey, history);

        // Clear old cache entries if cache gets too large (keep last 10)
        if (scoreHistoryCache.current.size > 10) {
          const firstKey = scoreHistoryCache.current.keys().next().value;
          if (firstKey) {
            scoreHistoryCache.current.delete(firstKey);
          }
        }

        setScoreHistory(history);
      } catch (error) {
        logger.error('Error calculating score history:', error);
        setScoreHistory([]);
      } finally {
        setIsLoadingHistory(false);
      }
    };

    calculateScoreHistory();
  }, [trades, historyPeriod, selectedTags, settings, selectedDate, timePeriod]);

  // Calculate multi-period scores for overview
  useEffect(() => {
    if (trades.length === 0) {
      logger.warn('ScoreSection: No trades available for multi-period calculation');
      setMultiPeriodScores(null);
      setIsLoadingMultiPeriod(false);
      return;
    }

    const calculateMultiPeriodScores = async () => {
      setIsLoadingMultiPeriod(true);
      try {
        logger.debug('ScoreSection: Starting multi-period score calculation', {
          tradesCount: trades.length,
          selectedDate: selectedDate.toISOString(),
          selectedTags: selectedTags.length
        });

        // Ensure score service has the latest settings before calculation
        const updatedSettings = { ...settings, selectedTags };
        scoreService.updateSettings(updatedSettings);
        const scores = await scoreService.calculateMultiPeriodScore(trades, selectedDate, updatedSettings);

        logger.debug('ScoreSection: Multi-period scores calculated successfully', {
          dailyScore: scores.daily.currentScore.overall,
          weeklyScore: scores.weekly.currentScore.overall,
          monthlyScore: scores.monthly.currentScore.overall,
          yearlyScore: scores.yearly.currentScore.overall
        });

        setMultiPeriodScores(scores);
      } catch (error) {
        logger.error('ScoreSection: Error calculating multi-period scores:', error);
        console.error('ScoreSection multi-period calculation error details:', {
          error,
          tradesCount: trades.length,
          selectedDate: selectedDate.toISOString(),
          sampleTrade: trades[0]
        });
        setMultiPeriodScores(null);
      } finally {
        setIsLoadingMultiPeriod(false);
      }
    };

    calculateMultiPeriodScores();
  }, [trades, selectedDate, selectedTags, settings]);




  const handleTabChange = (_: React.SyntheticEvent, newValue: number) => {
    setActiveTab(newValue);
  };

  // Define tabs for RoundedTabs component
  const tabs = [
    { label: 'Overview' },
    { label: 'History' },
    { label: 'Patterns' },
    { label: 'Settings' }
  ];

  const handleHistoryPeriodChange = (newPeriod: 'daily' | 'weekly' | 'monthly' | 'yearly') => {
    setHistoryPeriod(newPeriod);
  };

  // Handler for opening breakdown modal
  const handleScoreCardClick = (period: 'daily' | 'weekly' | 'monthly' | 'yearly') => {
    if (!multiPeriodScores) return;

    const analysis = multiPeriodScores[period];
    if (analysis) {
      setSelectedBreakdownData({ analysis, period });
      setBreakdownModalOpen(true);
    }
  };

  // Handler for closing breakdown modal
  const handleCloseBreakdownModal = () => {
    setBreakdownModalOpen(false);
    setSelectedBreakdownData(null);
  };

  const handleSettingsChange = (newSettings: ScoreSettings) => {
    setSettings(newSettings);
  };

  const handleSettingsSave = async (tagsOverride?: string[]) => {
    if (!onUpdateCalendarProperty) return;

    setIsSaving(true);
    try {
      const updatedSettings = { ...settings, selectedTags: tagsOverride ?? selectedTags };
      await onUpdateCalendarProperty(calendarId, (calendar) => ({
        ...calendar,
        score_settings: updatedSettings
      }));
    } catch (error) {
      logger.error('Error saving score settings:', error);
    } finally {
      setIsSaving(false);
    }
  };

  const handleTagsChange = async (tags: string[]) => {
    setSelectedTags(tags);

    // Auto-save tags to calendar when they change
    if (onUpdateCalendarProperty) {
      // Pass the new tags directly to ensure they're saved
      await handleSettingsSave(tags);
    }
  };

  // Fallback to localStorage if no calendar settings are provided
  useEffect(() => {
    if (!scoreSettings && !onUpdateCalendarProperty) {
      const savedSettings = localStorage.getItem('scoreSettings');
      if (savedSettings) {
        try {
          const parsedSettings = JSON.parse(savedSettings);
          setSettings(parsedSettings);
          setSelectedTags(parsedSettings.selectedTags || []);
        } catch (error) {
          logger.error('Error loading saved settings from localStorage:', error);
        }
      }
    }
  }, [scoreSettings, onUpdateCalendarProperty]);

  if (trades.length === 0) {
    return (
      <Paper sx={{ p: { xs: 2, sm: 3 } }}>
        <Typography variant={isXs ? 'subtitle1' : 'h6'} gutterBottom>
           Trading Score
        </Typography>
        <Alert severity="info">
          Start adding trades to see your trading score and performance analysis.
          The scoring system will evaluate your consistency, risk management, performance, and discipline.
        </Alert>
      </Paper>
    );
  }

  if (trades.length < settings.thresholds.minTradesForScore) {
    return (
      <Paper sx={{ p: { xs: 2, sm: 3 } }}>
        <Typography variant={isXs ? 'subtitle1' : 'h6'} gutterBottom>
           Trading Score
        </Typography>
        <Alert severity="warning">
          You need at least {settings.thresholds.minTradesForScore} trades to calculate your score.
          Current trades: {trades.length}
        </Alert>
      </Paper>
    );
  }

  return (
    <Paper
      data-testid="score-section"
      sx={{
        p: 0,
        backgroundColor: theme.palette.mode === 'dark'
          ? alpha(theme.palette.background.paper, 0.8)
          : theme.palette.background.paper,
        borderRadius: 2,
        boxShadow: theme.shadows[2],
        border: `1px solid ${theme.palette.mode === 'dark'
          ? 'rgba(204, 204, 204, 0.1)'
          : alpha(theme.palette.common.black, 0.1)}`
      }}
    >
      <Box sx={{ px: { xs: 2, sm: 3 }, pt: { xs: 2, sm: 3 } }}>
        <Stack direction={{ xs: 'column', sm: 'row' }} alignItems={{ xs: 'flex-start', sm: 'center' }} justifyContent="space-between" mb={{ xs: 2, sm: 3 }}>
          <Typography
            variant="h6"
            sx={{
              color: theme.palette.text.primary,
              fontWeight: 600
            }}
          >
             Trading Score Analysis
          </Typography>

          <RoundedTabs
            tabs={tabs}
            activeTab={activeTab}
            onTabChange={handleTabChange}
          />
        </Stack>
      </Box>

      <Box sx={{ px: { xs: 2, sm: 3 }, pb: { xs: 2, sm: 3 } }}>
        {/* Overview Tab */}
        <TabPanel value={activeTab} index={0}>
          <Box>
            {isLoadingMultiPeriod && (
              <LinearProgress
                sx={{
                  mb: 2,
                  borderRadius: 1
                }}
              />
            )}
            <Stack spacing={4}>
              {/* Multi-period score cards */}
              {multiPeriodScores ? (
              <Box>

                <Typography
                  variant="body2"
                  sx={{
                    color: theme.palette.text.secondary,
                    mb: 1,
                    fontStyle: 'italic'
                  }}
                >
                  Click on any score card to see detailed breakdown
                </Typography>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} sx={{ flexWrap: 'wrap' }}>
                  <Box
                    onClick={() => handleScoreCardClick('daily')}
                    sx={{
                      cursor: 'pointer',
                      transition: 'transform 0.2s',
                      '&:hover': { transform: 'translateY(-2px)' },
                      flex: { xs: '1', sm: '1 1 calc(50% - 8px)', md: '1 1 calc(25% - 12px)' }
                    }}
                  >
                    <ScoreCard
                      score={multiPeriodScores.daily.currentScore}
                      trend={multiPeriodScores.daily.trend}
                      period="daily"
                      compact
                    />
                  </Box>
                  <Box
                    onClick={() => handleScoreCardClick('weekly')}
                    sx={{
                      cursor: 'pointer',
                      transition: 'transform 0.2s',
                      '&:hover': { transform: 'translateY(-2px)' },
                      flex: { xs: '1', sm: '1 1 calc(50% - 8px)', md: '1 1 calc(25% - 12px)' }
                    }}
                  >
                    <ScoreCard
                      score={multiPeriodScores.weekly.currentScore}
                      trend={multiPeriodScores.weekly.trend}
                      period="weekly"
                      compact
                    />
                  </Box>
                  <Box
                    onClick={() => handleScoreCardClick('monthly')}
                    sx={{
                      cursor: 'pointer',
                      transition: 'transform 0.2s',
                      '&:hover': { transform: 'translateY(-2px)' },
                      flex: { xs: '1', sm: '1 1 calc(50% - 8px)', md: '1 1 calc(25% - 12px)' }
                    }}
                  >
                    <ScoreCard
                      score={multiPeriodScores.monthly.currentScore}
                      trend={multiPeriodScores.monthly.trend}
                      period="monthly"
                      compact
                    />
                  </Box>
                  <Box
                    onClick={() => handleScoreCardClick('yearly')}
                    sx={{
                      cursor: 'pointer',
                      transition: 'transform 0.2s',
                      '&:hover': { transform: 'translateY(-2px)' },
                      flex: { xs: '1', sm: '1 1 calc(50% - 8px)', md: '1 1 calc(25% - 12px)' }
                    }}
                  >
                    <ScoreCard
                      score={multiPeriodScores.yearly.currentScore}
                      trend={multiPeriodScores.yearly.trend}
                      period="yearly"
                      compact
                    />
                  </Box>
                </Stack>
              </Box>
            ) : (
              <Alert severity="warning">
                <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 600 }}>
                   No Score Data Available
                </Typography>
                <Typography variant="body2">
                  Unable to calculate multi-period scores. This could be because:
                  <br /> No trades found for the current periods
                  <br /> Insufficient trade data for analysis
                  <br /> All trades are outside the selected date range
                </Typography>
              </Alert>
            )}



            {/* Quick recommendations */}
            {scoreAnalysis && scoreAnalysis.recommendations.length > 0 && (
              <Alert
                severity="info"
                sx={{
                  backgroundColor: theme.palette.mode === 'dark'
                    ? alpha(theme.palette.info.main, 0.1)
                    : alpha(theme.palette.info.main, 0.05),
                  border: `1px solid ${alpha(theme.palette.info.main, 0.2)}`,
                  borderRadius: 2,
                  '& .MuiAlert-icon': {
                    color: theme.palette.info.main
                  }
                }}
              >
                <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 600 }}>
                   Quick Tip
                </Typography>
                <Typography variant="body2">
                  {scoreAnalysis.recommendations[0]}
                </Typography>
              </Alert>
            )}

            {/* No content fallback */}
            {!multiPeriodScores && !scoreAnalysis && (
              <Alert severity="info">
                <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 600 }}>
                   Get Started with Scoring
                </Typography>
                <Typography variant="body2">
                  To see your trading score analysis:
                  <br /> Add at least {settings.thresholds.minTradesForScore} trades
                  <br /> Ensure trades have dates within the current periods
                  <br /> Include session, tags, and risk/reward data for better analysis
                </Typography>
              </Alert>
            )}
            </Stack>
          </Box>
        </TabPanel>

        {/* Score History Tab */}
        <TabPanel value={activeTab} index={1}>
          <ScoreHistory
            history={scoreHistory}
            period={historyPeriod}
            onPeriodChange={handleHistoryPeriodChange}
            availablePeriods={availablePeriods}
            isLoading={isLoadingHistory}
          />
        </TabPanel>

        {/* Pattern Analysis Tab */}
        <TabPanel value={activeTab} index={2}>
          <TagPatternAnalysis
            trades={trades}
            selectedDate={selectedDate}
            settings={settings}
          />
        </TabPanel>

        {/* Settings Tab */}
        <TabPanel value={activeTab} index={3}>
          <Stack spacing={3}>
            <ScoreSettingsComponent
              settings={settings}
              onSettingsChange={handleSettingsChange}
              onSave={handleSettingsSave}
              isSaving={isSaving}
              trades={trades}
              selectedTags={selectedTags}
              onTagsChange={handleTagsChange}
              allTags={allTags}
            />
          </Stack>
        </TabPanel>
      </Box>

      {/* Score Breakdown Modal */}
      <Dialog
        open={breakdownModalOpen}
        onClose={handleCloseBreakdownModal}
        maxWidth="lg"
        fullWidth
        fullScreen={isXs}
        sx={{
          '& .MuiDialog-paper': {
            backgroundColor: theme.palette.mode === 'dark'
              ? alpha(theme.palette.background.paper, 0.95)
              : theme.palette.background.paper,
            borderRadius: 2,
            maxHeight: '90vh'
          },
          '& .MuiDialogContent-root': {
            ...scrollbarStyles(theme)
          }
        }}
      >
        <DialogTitle sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          pb: 1,
          fontWeight: 600
        }}>
          {selectedBreakdownData ?
            ` ${selectedBreakdownData.period.charAt(0).toUpperCase() + selectedBreakdownData.period.slice(1)} Score Analysis`
            : 'Score Analysis'
          }
          <IconButton
            onClick={handleCloseBreakdownModal}
            sx={{
              color: theme.palette.text.secondary,
              '&:hover': {
                backgroundColor: alpha(theme.palette.text.secondary, 0.1)
              }
            }}
          >
            <Close />
          </IconButton>
        </DialogTitle>
        <DialogContent sx={{ pt: 1 }}>
          {selectedBreakdownData && (
            <ScoreBreakdown
              breakdown={selectedBreakdownData.analysis.breakdown}
              pattern={selectedBreakdownData.analysis.pattern}
              recommendations={selectedBreakdownData.analysis.recommendations}
              strengths={selectedBreakdownData.analysis.strengths}
              weaknesses={selectedBreakdownData.analysis.weaknesses}
            />
          )}
        </DialogContent>
      </Dialog>
    </Paper>
  );
};

export default ScoreSection;
